*{
  box-sizing: border-box;
}
.App{
  margin:auto;
  width:900px;
}
header{
  display: flex;
  margin:10px;
  padding:5px;
  border-radius: 4px;
  border:1px solid #ccc;
  justify-content: space-between;
}
.staffGrid{
  display:flex;
  flex-basis: 70%;
  flex-wrap: wrap;
  border-radius: 4px;
  border:1px solid #ccc;
  padding:5px;
  margin:10px;
}
.sideBar{
  flex-basis: 30%;
  border-radius: 4px;
  border:1px solid #ccc;
  padding:5px;
  margin:10px;
}
main{
  width:100%;
  display:flex;
  flex-wrap: nowrap;
  justify-content: space-between;
}
.grid{
  flex-basis: 50%;
  flex-wrap: wrap;
  padding:15px;
}
label{
  display:block;
}
.btn{
  -webkit-appearance: none;
  background: none;
  border: none;
  font-size: 0.8rem;
  color: #fff;
  text-transform: uppercase;
  padding: 10px 25px;
  margin: 5px 0;
  border-radius: 4px;
}
input[type=text]{
  padding:5px;
  margin:2px 5px 5px 0;
  font-size: 0.8rem;
}
.del{
  background-color: #880000;
}
.add{
  background-color: #006600;
}
.edit{
  background-color: #d7912f;
}
